footer{
    color: white;
    background-color: #111;
    padding-top: 3em;
    padding-left: 2em;
    padding-right: 2em;
    position: relative;
    bottom: 0;
    width: 100%;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flexbox;
    display: flex;
    flex-flow: row wrap;
}

.footer > *{
    flex: 1 100%;
}

.footer-left{
    margin-right: 1.25em;
    margin-bottom: 2em;
}

.footer-left .img {
    width: 20%;
}
h2{
    font-weight: 600;
    font-size: 17px;
}

.footer ul {
    list-style: none;
    padding-left: 0;
}

.footer li {
    line-height: 2em;
}
.products{
    color: #fff;
}

.footer a {
    text-decoration: none;
}

.footer-right{
    display: flex;
    flex-flow: row wrap;
}

.footer-right > *{
    flex: 1 50%;
    margin-right: 1.25em;
}

.box a {
    color: #999;
}

.footer-bottom {
    background-color: #111;
    text-align: center;
    color: #999;
    padding-top: 50px;
}

.footer-left p {
    padding-right: 20%;
    color: #999;
}
.name{
    height: 50px;
}
.socials{
    padding-top: 1em;
    display: flex;
    justify-content: space-evenly;
}

.svg {
    width: 30px;
    height: 30px;
    filter: invert(1);
    transition: 0.5s ease;
}

.svg:hover{
    filter: invert(0.6);
}

@media screen and (min-width: 650px){
    .footer-right > *{
        flex: 1;
    }
    .footer-left{
        flex: 1 0px;
    }
    .footer-right {
        flex: 2 0px;
    }
}

@media (max-width: 1200px){
    .footer{
        padding: 15px;
    }
    .footer-left p {
        display: none;
    }
}
