#product{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.card {
    min-width: 300px;
    max-width: 360px;
    max-height: 460px;
    min-height: 400px;
    background-color: #F6F6F6;
    border: 1px solid #eee;
    overflow: hidden;
    padding: 10px;
    box-shadow: none;
    margin: 10px;
    transition: 0.3s linear;
    border-radius: 25px;
}

.card:hover{
    box-shadow: 6px 18px 35px #bbb;
}

.card img{
    width: 100%;
    height: 100%;
    padding-left: 0%;
    max-height: 250px;
    display: block;
    object-fit: cover;
    transition: 0.3s linear;
}


.card h3{
    white-space: nowrap;
    overflow: hidden;
    display: block;
    text-overflow: ellipsis;
    text-transform: uppercase;
    margin: 10px 0;
}

.card h3 a {
    text-decoration: none;
    color: #333;
}

.card h3 a:hover{
    color: black;
}

.card span {
    padding-left: 12px;
    color: black;
}

.card__can{
    padding-right: 10px;
    color: #777;
    text-decoration: line-through;
}

.card button{
    margin: 10px 0;
    border: none;
    outline: none;
    background: #333;
    color: white;
    width: 100%;
    height: 40px;
    display: block;
    cursor: pointer;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin: 15px 0;
    border-radius: 10px;
}

