.profile {
    padding: 20px;
}

.profile h1{
    font-size: 50px;
    font-weight: 700;
    letter-spacing: 1px;
    word-spacing: -5px;
    padding-bottom: 30px;
}
.profile__card{
        min-width: 360px;
        min-height: 400px;
        background-color: #F6F6F6;
        border: 1px solid #eee;
        overflow: hidden;
        padding: 10px;
        box-shadow: none;
        margin: 10px;
        transition: 0.3s linear;
        border-radius: 25px;
}
.profile__display{
    align-items: center;
    display: flex;
    flex-direction: column;
    
}

.profile__content{
    width: 100%;
    align-items: center;
    display: flex;
    /*justify-content: center;*/
    flex-direction: column;
    padding-top: 20px;
}

.profile__display img {
    height: 100px;
    width: 100px;
    border-radius: 46px;
}

.profile__display h1{
    font-size: 50px;
    font-weight: 700;
    letter-spacing: 3px;
    word-spacing: 5px;
    padding-bottom: 30px;
}
.profile__display h2{
    font-size: 22px;
    font-weight: 500;
    color: #555;
}
