.detail__page{
    background-color: #F6F6F6;
    width: 95%;
    display: flex;
    min-width: 300px;
    min-height: 400px;
    justify-content: start;
    border: 1px solid #eee;
    overflow: hidden;
    padding: 10px;
    box-shadow: none;
    margin: 10px;
    transition: 0.2s linear;
}

.detail__img{
    flex: 0 50%;
    width: 50%;
    display: flex;
    align-self: start;
    flex-wrap: wrap;
    padding: 10px;
}

.detail__img img{
    /*border: 1px solid #000;*/
    max-width: 400px;
    min-width: 200px;
    padding: 4px;
    width: 100%;
    height: 100%;
    max-height: 400px;
    min-height: 200px;
    display: block;
    object-fit: cover;
}

.detail__info{
    margin-right: 80px;
    width: 50%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.detail__info h1{
    font-size: 65px;
    transition: 0.2s linear;
}
.detail__info span{
    font-size: 50px;
}
.detail__info h4{
    color: #333;
    font-size: 15px;
    font-weight: 100;
    transition: 0.5s linear;
}

.detail__buynow{
    margin: 10px 0;
    border: none;
    outline: none;
    background: #333;
    color: white;
    width: 100%;
    border-radius: 40px;
    font-size: 30px;
    font-weight: 300;
    height: 60px;
    display: block;
    cursor: pointer;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin: 15px 0;
    transition: 0.5s;
}


.detail__button{
    margin: 10px 0;
    border: 2px solid #333;
    border-radius: 40px;
    align-items: stretch;
    outline: none;
    background: white;
    color: #333;
    font-size: x-large;
    font-weight: 700;
    width: 100%;
    height: 60px;
    display: block;
    cursor: pointer;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin: 15px 0;
}

@media screen and (max-width: 650px){
    .detail__page{
        padding: 10px;
        width: 95%;
        align-items: center;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .detail__info h1{
        font-size: 20px;
    }
    .detail__scroll{
        min-width: 100%;
    }

    .detail__img{
        width: 100%;
        display: flex;
        overflow: scroll;
        flex-wrap: nowrap;
        object-fit: cover;

    }
    .detail__img .img{
        margin-left: 10%;
        margin-right: 10%;
        display: inline-block;
        flex: 1 0 auto;
    }
    
    .detail__info{
        width: 100%;
    }
    .detail__info h4{
        display: none;
    }

}