.about{
    min-height: 75vh;
    max-height: 100vh;
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    top: 0;
    padding-top: 20px;
}

.about h1{
    font-size: 50px;
    font-weight: 700;
    letter-spacing: 3px;
    word-spacing: 5px;
    padding-bottom: 30px;
}

.about p{
    text-align: justify;
    font-size: 23px;
    font-weight: 600;
    color: #333;
    padding-left: 50px;
    padding-right: 50px;
    letter-spacing: 2.5px;
    padding-top: 15px;
    padding-bottom: 5px;
}

.about h2{
    font-size: 22px;
    font-weight: 500;
    color: #555;
}

.about h3{
    font-size: 25px;
    font-size: 600;
}

@media screen and (max-width: 650px){
    .about p{
        letter-spacing: 2px;
        text-align: center;
    }
    .about h2 {
        padding-top: 30px;
    }
}