header{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
}

.header{
    min-height: 70px;
    max-height: 120px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    overflow: hidden;
    padding-left: 10px;
    padding-right: 10px;
}


.header__top{
    justify-content: space-between;
    display: flex;
    width: 100%;
    background: #555;
    height: 30px;
}

@font-face{
    font-family: 'sp-pro';
    src: url(../../fonts/sf-pro-display_semibold.ttf);
    font-style: normal;
    font-weight: 600;
}

@font-face {
    font-family: 'DrukWide';
    src: url(../../fonts//DrukWide-Heavy.ttf);
    font-style: oblique;
}

.header__top h6{
    padding-top: 5px;
    font-family: sp-pro;
    padding-right: 10px;
    padding-left: 10px;
    line-height: 2;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 11px;
    text-decoration: none;
    color: white;
    text-align: center;
}

header nav {
    display: flex;
    align-items: center;
}

nav ul li {
    
    list-style: none;
    display: inline-block;
    padding-right: 10px;
}

nav ul li a {
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 700;
    text-decoration: none;
    text-transform: uppercase;
    color: #555;
    padding: 0 5px;
    transition: 0.5s linear;
}

nav ul li a:hover{
    color: black;
}

.nav-cart{
    cursor: pointer;
    position: relative;
}

.nav-cart span {
    position: absolute;
    top: 0px;
    right: -4px;
    background: crimson;
    font-size: 10px;
    color: white;
    padding: 3px 5px;
    border-radius: 50%;
    z-index: -1;
}

header .menu,
header .close{
    cursor: pointer;
    display: none;
}

.header__last {
    display: flex;
}

.search__box{
    padding-right: 10px;
}


@media(max-width: 650px){
    header ul {
        position: fixed;
        top: -110%;
        left: 0;
        width: 100%;
        height: 100vh;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        flex-direction: column;
        background: white;
        z-index: 99;
        opacity: 0.97;
        transition: .6s ease;
    }
    .header__top h6{
        font-size: 10.5px;
    }
    header .menu,
    header .close {
        display: block;   
    }

    header .close {
        position: absolute;
        top: 10px;
        right: 20px;
    }

    header ul.toggle{
        top: 0;
    }

    header .logo {
        padding-left: 70px;
    }
}